import { createRouter, createWebHistory } from 'vue-router'
import BodyView from "@/layout/BodyView.vue"

import auth from '@/services/auth';


const Dashboard = () => import('@/pages/Dashboard.vue');
const Instances = () => import('@/pages/Instances.vue');
const Subscribe = () => import('@/pages/Subscribe.vue');
// const Profile = () => import('@/pages/Profile.vue');
const Login = () => import('@/pages/Login.vue');
const Register = () => import('@/pages/Register.vue');
const routes = [
  {
    path: '/',
    component: BodyView,
    children: [
      { path: '', name: 'dashboard', component: Dashboard, meta: { requiresAuth: true } },
      { path: 'instances', name: 'instances', component: Instances, meta: { requiresAuth: true } },
      { path: 'subscribe', name: 'subscribe', component: Subscribe, meta: { requiresAuth: true } },
      //   { path: 'profile', name: 'profile', component: Profile, meta: { requiresAuth: true } }
    ]
  },
  { path: '/login', name: 'login', component: Login, meta: { guest: true } },
  { path: '/register', name: 'register', component: Register, meta: { guest: true } },
  // fallback
  { path: '/:pathMatch(.*)*', redirect: '/' }
];

const router = createRouter({
  history: createWebHistory(),
  routes
});

router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some(r => r.meta && r.meta.requiresAuth);
  const isGuestOnly = to.matched.some(r => r.meta && r.meta.guest);
  const logged = auth.isAuthenticated();

  if (requiresAuth && !logged) {
    return next({ name: 'login', query: { redirect: to.fullPath } });
  }
  if (isGuestOnly && logged) {
    return next({ name: 'dashboard' });
  }
  next();
});
export default router
